import "./testimonials.scss";

export default function Testimonials() {
  const data = [
    {
      id: 1,
      name: "Mandeep Bharara",
      title: "Graphic Designer",
      img:
        "https://fiverr-res.cloudinary.com/image/upload/f_auto,q_auto,t_profile_small/v1/attachments/profile/photo/ac14d5e23fc7c3ab0f8e94a754a6d03a-1609698309121/06031c7f-5875-4b09-83ae-8d9359ca67cc.jpg",
      icon: "assets/youtube.png",
      desc:
        "It was great working with Priyansh. He is very talented and open to listen the requirement of the Client and deliver on time. I am happy to work with him and ready to work with him again in future.",
    },
    {
      id: 2,
      name: "Studio Knecht",
      title: "Senior Developer",
      img:
        "https://fiverr-res.cloudinary.com/image/upload/f_auto,q_auto,t_profile_small/v1/profile/photos/2158327/original/FiverrBusiness.jpg",
      icon: "assets/twitter.png",
      desc:
        "completed a large amount of work for me and delivered on time at a great price. great attitude and hard working. many thanks!",
    featured: true,

    },
    {
      id: 3,
      name: "Mohammad Arif",
      title: "Developer",
      img:
        "https://fiverr-res.cloudinary.com/image/upload/f_auto,q_auto,t_profile_small/v1/attachments/profile/photo/1a501cc79799612e90d21f240aa1ff7c-1071838221621481644304/JPEG_20210520_053402_1555045053070346104.jpg",
      icon: "assets/linkedin.png",
      desc:
        "He has vast knowledge in designing websites and adding functionalities . He has covered all my requirements within short span of time and delivered value added service for me. Really appreciate your effort and dedication towards the project .Thank you.. Looking forward to working with you..",
    },
  ];
  return (
    <div className="testimonials" id="testimonials">
      <h1>Testimonials</h1>
      <div className="container">
        {data.map((d) => (
          <div className={d.featured ? "card featured" : "card"}>
            <div className="top">
              <img src="assets/right-arrow.png" className="left" alt="" />
              <img
                className="user"
                src={d.img}
                alt=""
              />
              <img className="right" src={d.icon} alt="" />
            </div>
            <div className="center">
              {d.desc}
            </div>
            <div className="bottom">
              <h3>{d.name}</h3>
              <h4>{d.title}</h4>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
