import './Intro.scss'
import {init} from 'ityped'
import { useEffect, useRef } from 'react'

function Intro() {

    const textR = useRef();

    useEffect(() => {
        init(textR.current, {
            showCursor: true,
            backDelay: 1500,
            backSpeed: 60,
             strings: ['Designer', 'Developer', 'Content Creator']
            });
    }, [])
    return (
        <div className="intro" id="intro">
            <div className="left">
                <div className="img-cont">
                    <img src="assets/prp.png" alt="" />
                </div>

            </div>
            <div className="right">
                <div className="wrapper">
                    <h2>Hi There, I'm</h2>
                    <h1>Priyansh</h1>
                    <h3>Freelance <span ref ={textR}></span></h3>
                </div>
                <a href="#portfolio">
                    <img src="assets/down.png" alt="" />
                </a>

            </div>
            
        </div>
    )
}

export default Intro
